import Styles from './game-button.module.css';
interface Props {
  backgroundImage: string;
  label: string;
  onClick?: any;
}

const GameButton = (props: Props) => {
  const style = {
    backgroundImage: props.backgroundImage,
  }
  return (
    <button onClick={props.onClick} style={style} className={Styles.gameButton}>{props.label}</button>
  )
}

export default GameButton;