import { useTranslation } from 'react-i18next';
import Styles from './App.module.css';
import logo from './assets/images/logo.png';
import GameButton from './components/game-button/game-button';
import paypalImage from './assets/images/paypal.svg';
import ideaImage from './assets/images/idea.svg';

const blueBackground = 'linear-gradient(160deg,#1086e8,#b800e6)';

const goToGluglup = () => {
  window.open('https://gluglup.com', '_blank');
}

const openIdeas = () => {
  window.open('https://forms.gle/QpnfZpJDRDagirLS6', '_blank');
}

const openPaypal = () => {
  window.open('https://www.paypal.com/donate/?hosted_button_id=MNEBAFDAWSCN6', '_blank');
}

function App() {
  const { t } = useTranslation();
  return (
    <div className={Styles.mainBackground}>
      <img alt="paypal" className={Styles.paypalImage} onClick={openPaypal} src={paypalImage} />
      <img alt="idea" className={Styles.ideaImage} onClick={openIdeas} src={ideaImage} />
      <div className={Styles.container}>
        <div className={Styles.logoContainer}>
          <img className={Styles.logo} alt="logo" src={logo} />
        </div>
        <div className={Styles.messageContainer}>
          <div className={Styles.titleContainer}>
            <label className={Styles.messageTitle}>{t('title')}</label>
          </div>
          <p className={Styles.message}>
            {t('message')}
          </p>
        </div>
        <div className={Styles.buttonsContainer}>
            <GameButton onClick={goToGluglup} backgroundImage={blueBackground} label={t('goToGluglup')}></GameButton>
          </div>
      </div>

    </div>
  );
}

export default App;
